.wrapper {
    width: 420px;
    background: transparent;
    color: #fffefe;
    border-radius: 10px;
    padding: 30px 40px;
}
.wrapper h1{
    font-size: 36px;
    text-align: center;

}
.wrapper .input-box {
    position: relative;
    width: 100%;
    height: 50px;
    margin: 30px 0;
}
.input-box input {
    width: 100%;
    height: 100%;
    background: transparent;
    border: none;
    outline: none;
    border: 2px solid rgb(250, 250, 250);
    border-radius: 40px;
    font-size: 16px;
    color: #fff;
    padding: 20px 45px 20px 20px;

}
.input-box input ::placeholder {
    color: #e5d9d9;
}
.input-box .icon {
    position: absolute;
    right: 20px;
    top: 50%;
    transform: translateY(-50%);
    font-size: 16px;
}
.wrapper .remember-forgot {
    display: flex;
    justify-content: space-between;
    font-size: 15px;
    margin: -15px 0 15px;
}
.remember-forgot label input {
    accent-color: #fff;
    margin-right: 4px;
}
.remember-forgot a {
    color: #fff;
    text-decoration: none;
}
.remember-forgot a:hover {
text-decoration: underline; }

.wrapper button {
    width: 100%;
    height: 45px;
    background: #fff;
    border: none;
    border-radius: 40px;
    outline: none;
    box-shadow: 0 0 10px rgba(0, 0, 0, 0);
    cursor: pointer;
    font-size: 16px;
    color: #1b1717;
    font-weight: 700;
}
.wrapper .register-link {
    font-size: 15px;
    text-align: center;
    margin-top: 20px;
}
.register-link p a {
    color :#fff;
    text-decoration: none;
    font-weight: 600;
}
.register-link p a :hover {
    text-decoration: underline;
}